.list {
  margin: 1rem 0;
  font-size: 0.85rem;
}
.list ul {
  list-style: none;
  margin: 0;
  padding: 0;
  position: relative;
}
.list ul:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: auto;
  right: auto;
  height: 1px;
  width: 100%;
  background-color: #e7e7e7;
  display: block;
  z-index: 1;
  transform-origin: 50% 0%;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2) {
.list ul:before {
    transform: scaleY(0.5);
}
}
.list ul:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  right: auto;
  top: auto;
  height: 1px;
  width: 100%;
  background-color: #e7e7e7;
  display: block;
  z-index: 1;
  transform-origin: 50% 100%;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2) {
.list ul:after {
    transform: scaleY(0.5);
}
}
.list .list-title,
.list .list-append {
  font-size: .8rem;
  color: #999;
  padding: .2rem .75rem;
}
.list .list-append {
  padding: .3rem .75rem;
  font-size: .7rem;
}
.list li + li .item-content:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: auto;
  right: auto;
  height: 1px;
  width: 100%;
  background-color: #e7e7e7;
  display: block;
  z-index: 1;
  transform-origin: 50% 0%;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2) {
.list li + li .item-content:before {
    transform: scaleY(0.5);
}
}
.list .item-inner {
  width: 100%;
  display: flex;
  padding: 0 0 0 .75rem;
  position: relative;
}
.list .item-media {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: .25rem 0;
}
.list .item-content {
  width: 100%;
  position: relative;
  padding: .4rem .75rem .4rem 0;
}
.list .item-link .item-content {
  padding-right: 1.4rem;
}
.list .item-link .link-arrow {
  font-size: .7rem;
  position: absolute;
  right: .3rem;
  top: 50%;
  height: 1rem;
  width: .8rem;
  color: #c7c7c7;
  margin-top: -0.5rem;
}
.list .item-media + .item-content {
  margin-left: .75rem;
}
.list .item-title-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.list .label-switch {
  display: block;
}
.list .item-after {
  margin-left: .5rem;
  color: #999;
  flex-shrink: 1;
}
.list .item-subtitle {
  font-size: .75rem;
}
.list .item-text {
  color: #5f646e;
  font-size: .75rem;
}
.list .list-group-title {
  padding: 0 15px;
  background: #f7f7f7;
  color: #000;
  font-weight: 500;
  line-height: 1.1rem;
  height: 1.1rem;
}
.list .list-group-title + li .item-content:before {
  display: none;
}
.list .item-after .badge {
  vertical-align: .1rem;
}
label.label-checkbox {
  cursor: pointer;
}
label.label-checkbox .item-content {
  padding-right: 2rem;
}
label.label-checkbox i.icon-form-checkbox {
  width: 22px;
  height: 22px;
  position: absolute;
  right: .5rem;
  top: 50%;
  transform: translate3d(0, -50%, 0);
  border-radius: 22px;
  border: 1px solid #c7c7cc;
  box-sizing: border-box;
}
label.label-checkbox i.icon-form-checkbox:after {
  content: ' ';
  position: absolute;
  left: 50%;
  margin-left: -6px;
  top: 50%;
  margin-top: -4px;
  width: 12px;
  height: 9px;
}
label.label-checkbox input[type="checkbox"] {
  display: none;
}
label.label-checkbox input[type="checkbox"]:checked ~ i.icon-form-checkbox {
  border: none;
  background-color: #04BE02;
}
label.label-checkbox input[type="checkbox"]:checked ~ i.icon-form-checkbox:after {
  background: no-repeat center;
  background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg%20xmlns%3D'http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg'%20x%3D'0px'%20y%3D'0px'%20viewBox%3D'0%200%2012%209'%20xml%3Aspace%3D'preserve'%3E%3Cpolygon%20fill%3D'%23ffffff'%20points%3D'12%2C0.7%2011.3%2C0%203.9%2C7.4%200.7%2C4.2%200%2C4.9%203.9%2C8.8%203.9%2C8.8%203.9%2C8.8%20'%2F%3E%3C%2Fsvg%3E");
  -webkit-background-size: 12px 9px;
  background-size: 12px 9px;
}
label.label-radio {
  cursor: pointer;
}
label.label-radio input[type="checkbox"],
label.label-radio input[type="radio"] {
  display: none;
}
label.label-radio input[type="checkbox"] ~ .item-content,
label.label-radio input[type="radio"] ~ .item-content {
  padding-right: 2rem;
}
label.label-radio input[type="checkbox"]:checked ~ .icon-form-radio,
label.label-radio input[type="radio"]:checked ~ .icon-form-radio {
  content: " ";
  background: no-repeat center;
  background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg%20xmlns%3D'http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg'%20viewBox%3D'0%200%2013%2010'%3E%3Cpolygon%20fill%3D'%2304BE02'%20points%3D'11.6%2C0%204.4%2C7.2%201.4%2C4.2%200%2C5.6%204.4%2C10%204.4%2C10%204.4%2C10%2013%2C1.4%20'%2F%3E%3C%2Fsvg%3E");
  background-position: center;
  background-size: 15px 11px;
  width: 2rem;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
}
label.label-checkbox,
label.label-radio,
.item-link {
  transition: 300ms;
}
label.label-checkbox:active,
label.label-radio:active,
.item-link:active {
  transition: 0ms;
  background-color: #d9d9d9;
}
label.label-checkbox:active .item-content:after,
label.label-radio:active .item-content:after,
.item-link:active .item-content:after {
  background-color: transparent;
}



