.nav-secondary ~ .page-content {
  top: 4.4rem;
}
.nav-secondary {
  top: 2.2rem;
  position: absolute;
  right: 0;
  left: 0;
  z-index: 10;
  backface-visibility: hidden;
  height: 2.2rem;
}
.nav-secondary:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  right: auto;
  top: auto;
  height: 1px;
  width: 100%;
  background-color: #e7e7e7;
  display: block;
  z-index: 1;
  transform-origin: 50% 100%;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2) {
.nav-secondary:after {
    transform: scaleY(0.5);
}
}



