.result {
  padding: .75rem;
  text-align: center;
  height: 100%;
  position: relative;
}
.result .brand-icon {
  font-size: 5.5rem;
  color: #04BE02;
  line-height: 1;
  margin: 1rem 0 .5rem 0;
}
.result .result-error .brand-icon,
.result .result-forbidden .brand-icon {
  color: #f6383a;
}
.result h1 {
  font-size: 1rem;
  font-weight: normal;
  margin: 0 0 .5rem 0;
}
.result .text {
  margin: .2rem 0 0 0;
  color: #999;
  font-size: .75rem;
}
.result .buttons {
  margin-top: 1.5rem;
}
.result .buttons .button + .button {
  margin-top: .75rem;
}
.result .bottom {
  position: absolute;
  bottom: 1rem;
  left: 0;
  width: 100%;
  font-size: .7rem;
  color: #61749b;
}



