.footer-secondary {
  padding: .4rem;
  height: 2.8rem;
  position: absolute;
  width: 100%;
  bottom: 2.8rem;
}
.footer-secondary:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: auto;
  right: auto;
  height: 1px;
  width: 100%;
  background-color: #e7e7e7;
  display: block;
  z-index: 1;
  transform-origin: 50% 0%;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2) {
.footer-secondary:before {
    transform: scaleY(0.5);
}
}
.footer-secondary:after {
  display: none;
}
.footer-secondary ~ .page-content {
  bottom: 5.6rem;
}



