.page,
.view {
  box-sizing: border-box;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity .3s;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}
.slide-enter-active,
.slide-leave-active {
  opacity: 1;
  transform: translate3d(0, 0, 0);
  transition: all .3s;
}
.slide-enter {
  opacity: 0;
  transform: translate3d(70%, 0, 0);
}
.slide-leave-active {
  opacity: 0;
  transform: translate3d(-70%, 0, 0);
}
.transition-reverse .slide-enter {
  opacity: 0;
  transform: translate3d(-70%, 0, 0);
}
.transition-reverse .slide-leave-active {
  opacity: 0;
  transform: translate3d(70%, 0, 0);
}
.page-content {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
.content-padded {
  margin: .5rem;
}



