.footer {
  background-color: #f7f7f8;
  position: absolute;
  bottom: 0;
  z-index: 100;
}
.footer:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: auto;
  right: auto;
  height: 1px;
  width: 100%;
  background-color: #e7e7e7;
  display: block;
  z-index: 1;
  transform-origin: 50% 0%;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2) {
.footer:before {
    transform: scaleY(0.5);
}
}
.footer:after {
  display: none;
}
.footer ~ .page-content {
  bottom: 2.8rem;
}
.footer {
  bottom: 0;
  width: 100%;
  height: 2.8rem;
  padding: 0;
  z-index: 9000;
  transition-duration: 400ms;
  display: flex;
}
.footer:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: auto;
  right: auto;
  height: 1px;
  width: 100%;
  background-color: #e7e7e7;
  display: block;
  z-index: 1;
  transform-origin: 50% 0%;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2) {
.footer:before {
    transform: scaleY(0.5);
}
}
.footer:after {
  display: none;
}
.footer .footer-item {
  position: relative;
  height: 2.8rem;
  color: #929292;
  text-align: center;
  flex: 1;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.footer .footer-item.active,
.footer .footer-item:active {
  color: #04BE02;
}
.footer .footer-item .badge {
  position: absolute;
  top: .1rem;
  left: 50%;
  z-index: 100;
  height: .9rem;
  min-width: .9rem;
  padding: 0 .2rem;
  font-size: .6rem;
  line-height: .9rem;
  color: white;
  vertical-align: top;
  background: red;
  border-radius: .5rem;
  margin-left: .1rem;
}
.footer .footer-item .icon {
  top: .1rem;
  width: 1.3rem;
  height: 1.3rem;
  font-size: 1.3rem;
  line-height: 1.3rem;
  padding-top: 0;
  padding-bottom: 0;
  display: inline-block;
  vertical-align: middle;
}
.footer .footer-item .icon ~ label {
  display: block;
  font-size: 0.65rem;
  position: relative;
  top: 0.15rem;
}
.footer .icon {
  font-size: 1.2rem;
  line-height: 1.2rem;
}
.bar input[type="search"] {
  height: 1.45rem;
  margin: 0.3rem 0;
}



