.popup-modal.actions-modal {
  background: transparent;
}
.action-group {
  margin: 0.4rem;
}
.action-button,
.action-label {
  width: 100%;
  text-align: center;
  font-weight: normal;
  margin: 0;
  background: rgba(243, 243, 243, 0.95);
  box-sizing: border-box;
  display: block;
  position: relative;
}
.action-button:after,
.action-label:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  right: auto;
  top: auto;
  height: 1px;
  width: 100%;
  background-color: #d2d2d6;
  display: block;
  z-index: 1;
  transform-origin: 50% 100%;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2) {
.action-button:after,
  .action-label:after {
    transform: scaleY(0.5);
}
}
.action-button a,
.action-label a {
  text-decoration: none;
  color: inherit;
}
.action-button b,
.action-label b {
  font-weight: 500;
}
.action-button.color-gray,
.action-label.color-gray {
  color: #999;
}
.action-button.color-danger,
.action-label.color-danger {
  color: #f6383a;
}
.action-button.bg-danger,
.action-label.bg-danger {
  background: #f6383a;
  color: white;
}
.action-button.bg-danger:active,
.action-label.bg-danger:active {
  background: #f00b0d;
}
.action-button:first-child,
.action-label:first-child {
  border-radius: 0.2rem 0.2rem 0 0;
}
.action-button:last-child,
.action-label:last-child {
  border-radius: 0 0 0.2rem 0.2rem;
}
.action-button:last-child:after,
.action-label:last-child:after {
  display: none;
}
.action-button:first-child:last-child,
.action-label:first-child:last-child {
  border-radius: 0.2rem;
}
.action-button.disabled,
.action-label.disabled {
  opacity: 0.95;
  color: #999;
}
.action-button {
  cursor: pointer;
  line-height: 2.15rem;
  font-size: .9rem;
  color: #04BE02;
}
.action-button:active,
.action-button.active-state {
  background: #dcdcdc;
}
.action-label {
  font-size: 0.7rem;
  line-height: 1.3;
  min-height: 2.2rem;
  padding: 0.4rem 0.5rem;
  color: #5f646e;
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}



