.popover[data-v-0da3d6fc] {
  position: relative;
  position: absolute;
  width: auto;
  padding: 1px .3rem;
  background-color: white;
  text-align: center;
  font-size: .8rem;
  min-width: 3rem;
  max-width: 6rem;
  transition: opacity .3s;
  opacity: 1;
  display: block;
}
.popover[data-v-0da3d6fc]:after {
  content: " ";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 0.25rem;
  transform-origin: 0 0;
  z-index: 0;
  box-sizing: border-box;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2) {
.popover[data-v-0da3d6fc]:after {
    height: 200%;
    width: 200%;
    transform: scale(0.5);
    border-radius: 0.5rem;
}
}
.popover.popover-enter[data-v-0da3d6fc],
.popover.popover-leave-active[data-v-0da3d6fc] {
  opacity: 0;
}
.angle[data-v-0da3d6fc] {
  width: .5rem;
  height: .5rem;
  position: relative;
  background-color: white;
  position: absolute;
  bottom: -0.25rem;
  left: 50%;
  margin-left: -0.25rem;
  z-index: 1;
  transform: rotate(45deg);
  transform-origin: center center;
}
.angle[data-v-0da3d6fc]:after {
  content: " ";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 0;
  transform-origin: 0 0;
  z-index: 0;
  box-sizing: border-box;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2) {
.angle[data-v-0da3d6fc]:after {
    height: 200%;
    width: 200%;
    transform: scale(0.5);
    border-radius: 0;
}
}
.bottom .angle[data-v-0da3d6fc] {
  bottom: initial;
  top: -0.25rem;
}
.inner[data-v-0da3d6fc] {
  position: relative;
  z-index: 10;
  background-color: white;
}



