@font-face {
  font-family: 'iconfont-vuem';
  src: url('//at.alicdn.com/t/font_1472697628_3911114.eot');
  /* IE9*/
  src: url('//at.alicdn.com/t/font_1472697628_3911114.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */ url('//at.alicdn.com/t/font_1472697628_3911114.woff') format('woff'), /* chrome、firefox */ url('//at.alicdn.com/t/font_1472697628_3911114.ttf') format('truetype'), /* chrome、firefox、opera、Safari, Android, iOS 4.2+*/ url('//at.alicdn.com/t/font_1472697628_3911114.svg#iconfont-vuem') format('svg');
  /* iOS 4.1- */
}
.icon {
  font-style: normal;
  font-family: 'iconfont-vuem';
}
