.toast-modal[data-v-5230a9ab] {
  position: absolute;
  left: 50%;
  top: 50%;
  padding: .5rem 1.1rem;
  transform: translate3d(-50%, -50%, 0) scale(1.2);
  background: rgba(0, 0, 0, 0.6);
  z-index: 11000;
  border-radius: 0.25rem;
  transition: transform .3s, opacity .3s;
  color: white;
  text-align: center;
  min-width: 5.5rem;
  opacity: 1;
}
