.buttons-group,
.buttons-tab {
  z-index: 10;
  -ms-flex-item-align: center;
  -webkit-align-self: center;
  align-self: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-lines: single;
  -moz-box-lines: single;
  -webkit-flex-wrap: nowrap;
  -ms-flex-wrap: none;
  -ms-flex-wrap: nowrap;
  flex-wrap: nowrap;
}
.buttons-group {
  margin: 1rem 0;
}
.buttons-group .button {
  border-radius: 0 0 0 0;
  width: 100%;
  -webkit-box-flex: 1;
  -ms-flex: 1;
}
.buttons-group .button:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: auto;
  right: auto;
  width: 1px;
  height: 100%;
  background-color: #038c01;
  display: block;
  z-index: 1;
  transform-origin: 0% 50%;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2) {
.buttons-group .button:before {
    transform: scaleX(0.5);
}
}
.buttons-group .button.active {
  background-color: #038c01;
  color: white;
  z-index: 100;
}
.buttons-group .button:first-child {
  border-radius: 0.25rem 0 0 0.25rem;
}
.buttons-group .button:first-child:before {
  display: none;
}
.buttons-group .button:last-child {
  border-radius: 0 0.25rem 0.25rem 0;
}
.buttons-group .button.button-round:first-child {
  border-radius: 1.35rem 0 0 1.35rem;
}
.buttons-group .button.button-round:last-child {
  border-radius: 0 1.35rem 1.35rem 0;
}
.buttons-group.button-bordered {
  border-radius: .25rem;
  position: relative;
}
.buttons-group.button-bordered:after {
  content: " ";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  border: 1px solid #04BE02;
  border-radius: 0.25rem;
  transform-origin: 0 0;
  z-index: 0;
  box-sizing: border-box;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2) {
.buttons-group.button-bordered:after {
    height: 200%;
    width: 200%;
    transform: scale(0.5);
    border-radius: 0.5rem;
}
}
.buttons-group.button-bordered .button {
  position: relative;
  z-index: 1;
}
.buttons-group.button-bordered .button:before {
  background-color: #04BE02;
}
.buttons-group.button-bordered .button:after {
  display: none;
}
.buttons-group.button-bordered .button.active,
.buttons-group.button-bordered .button:active {
  background-color: #04BE02;
  color: white;
}
.buttons-group.button-bordered.button-round {
  position: relative;
}
.buttons-group.button-bordered.button-round:after {
  content: " ";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  border: 1px solid #04BE02;
  border-radius: 2rem;
  transform-origin: 0 0;
  z-index: 0;
  box-sizing: border-box;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2) {
.buttons-group.button-bordered.button-round:after {
    height: 200%;
    width: 200%;
    transform: scale(0.5);
    border-radius: 4rem;
}
}
.buttons-group.button-bordered.button-round .button:first-child {
  border-radius: 2rem 0 0 2rem;
}
.buttons-group.button-bordered.button-round .button:last-child {
  border-radius: 0 2rem 2rem 0;
}
.buttons-group.button-bordered.button-round.button-small {
  position: relative;
}
.buttons-group.button-bordered.button-round.button-small:after {
  content: " ";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  border: 1px solid #04BE02;
  border-radius: 1.6rem;
  transform-origin: 0 0;
  z-index: 0;
  box-sizing: border-box;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2) {
.buttons-group.button-bordered.button-round.button-small:after {
    height: 200%;
    width: 200%;
    transform: scale(0.5);
    border-radius: 3.2rem;
}
}
.buttons-group.button-bordered.button-round.button-small .button:first-child {
  border-radius: 1.6rem 0 0 1.6rem;
}
.buttons-group.button-bordered.button-round.button-small .button:last-child {
  border-radius: 0 1.6rem 1.6rem 0;
}
.buttons-group.button-bordered.button-round.button-large {
  position: relative;
}
.buttons-group.button-bordered.button-round.button-large:after {
  content: " ";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  border: 1px solid #04BE02;
  border-radius: 2.5rem;
  transform-origin: 0 0;
  z-index: 0;
  box-sizing: border-box;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2) {
.buttons-group.button-bordered.button-round.button-large:after {
    height: 200%;
    width: 200%;
    transform: scale(0.5);
    border-radius: 5rem;
}
}
.buttons-group.button-bordered.button-round.button-large .button:first-child {
  border-radius: 2.5rem 0 0 2.5rem;
}
.buttons-group.button-bordered.button-round.button-large .button:last-child {
  border-radius: 0 2.5rem 2.5rem 0;
}
.buttons-tab {
  background: white;
  position: relative;
}
.buttons-tab:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  right: auto;
  top: auto;
  height: 1px;
  width: 100%;
  background-color: #d0d0d0;
  display: block;
  z-index: 1;
  transform-origin: 50% 100%;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2) {
.buttons-tab:after {
    transform: scaleY(0.5);
}
}
.buttons-tab .button {
  color: #5f646e;
  font-size: 0.8rem;
  width: 100%;
  height: 2rem;
  line-height: 2rem;
  -webkit-box-flex: 1;
  -ms-flex: 1;
  border: 0;
  border-bottom: 1px solid transparent;
  border-radius: 0;
  background-color: transparent;
}
.buttons-tab .button.active {
  color: #04BE02;
  border-color: #04BE02;
  background: transparent;
  z-index: 100;
}



