.button {
  color: white;
  border: 0;
  background-color: #04BE02;
  text-decoration: none;
  text-align: center;
  display: block;
  border-radius: 0.25rem;
  line-height: 2rem;
  box-sizing: border-box;
  appearance: none;
  padding: 0 0.8rem;
  margin: 0;
  height: 2rem;
  white-space: nowrap;
  position: relative;
  text-overflow: ellipsis;
  font-size: 0.8rem;
  cursor: pointer;
}
input[type="submit"].button,
input[type="button"].button {
  width: 100%;
}
.button:active {
  background-color: #038c01;
}
.button.button-round,
.button-round .button,
.button.button-round:after,
.button-round .button:after {
  border-radius: 1.9rem;
}
.button.button-round.button-large:after,
