  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 0.25rem;
  transform-origin: 0 0;
  z-index: 0;
  box-sizing: border-box;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2) {
