.slide-wrap[data-v-50e16c20] {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 10000;
}
.side[data-v-50e16c20] {
  width: 13rem;
  height: 100%;
  z-index: 10000;
  position: absolute;
  transition: transform .3s;
  transform: translate3d(0, 0, 0);
}
.side.side-enter[data-v-50e16c20],
.side.side-leave-active[data-v-50e16c20] {
  transform: translate3d(-100%, 0, 0);
}
.panel[data-v-50e16c20] {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  background-color: white;
  transition: transform .3s;
}
.touching .panel[data-v-50e16c20] {
  transition: none;
}



